<template>
  <div class="container">
    <div class="data">
      <div class="table">
        <el-table style="width: 100%" :show-summary="showSummary" :data="dataSource"  stripe border :header-cell-style="tableHeader">
          <el-table-column
            v-for="column in columns"
            header-align="center"
            :key="column.prop"
            :prop="column.prop"
            :min-width="column.width"
            :label="column.label"
            :align="column.align">
          </el-table-column>
        </el-table>
      </div>
    </div>
  </div>
</template>

<script>
import {mapState} from 'vuex'
export default {
  name: 'CommonTable',
  computed: mapState({
    sectors: state => state.individual.sectors,
    tableHeader: state => state.app.tableHeader
  }),
  props: {
    showSummary: {
      type: Boolean,
      default: false
    },
    columns: {
      type: Array,
      required: true
    },
    dataSource: {
      type: Array,
      required: true
    }
  }
}
</script>

<style scoped>
</style>



